.banner {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(1, 13, 22, 0.75)), to(rgba(1, 13, 22, 0.75))),
    url("../images/backdrop.jpg") no-repeat center center fixed;
  background: -webkit-linear-gradient(rgba(1, 13, 22, 0.75), rgba(1, 13, 22, 0.75)),
    url("../images/backdrop.jpg") no-repeat center center fixed;
  background: -o-linear-gradient(rgba(1, 13, 22, 0.75), rgba(1, 13, 22, 0.75)),
    url("../images/backdrop.jpg") no-repeat center center fixed;
  background: linear-gradient(rgba(1, 13, 22, 0.75), rgba(1, 13, 22, 0.75)),
    url("../images/backdrop.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 45em;
  width: 98%;
  margin-left: 1%;
}

.banner-description-container {
  padding: 7% 0% 0% 30%;
}

.banner-description {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  line-height: 75px;
  text-align: center;
  font-size: 54px;
  color: white;
  width: 500px;
  text-shadow: 4px 4px black;
}

.banner-subhead {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  line-height: 75px;
  text-align: center;
  font-size: 30px;
  color: white;
  width: 500px;
  text-shadow: 4px 4px black;
}

.info-header {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  text-align: center;
  font-size: 32px;
}

.info-text {
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  text-align: left;
  font-size: 24px;
}

.info-flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 3%;
  margin: 3%;
  line-height: 44px;
  color: black;
  border-bottom: 1px solid #e6550d;
}

.text-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px;
}

.info-image {
  height: 500px !important;
  width: 500px !important;
  margin: 10px;
}

.mock-icon {
  font-size: 28px;
  font-weight: 700;
}

.last-item {
  line-height: 44px;
  color: black;
  padding: 3% 15%;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  text-align: left;
  font-size: 24px;
  border-top: 1px solid #e6550d;
  border-bottom: 1px solid #e6550d;
}

@media (max-width: 768px) {
  .banner {
    height: 20em;
  }

  .banner-description-container {
    padding: 10% 0% 0% 15%;
  }

  .banner-description {
    font-size: 28px;
  }

  .banner-subhead {
    font-size: 20px;
    text-align: left;
  }

  .info-text {
    font-size: 22px;
  }
}

@media (max-width: 480px) {
  .banner-description-container {
    padding: 10% 0% 0% -10%;
  }

  .banner-description {
    text-align: left;
  }
}



